@import "./variables";

main {
    width: 100%;
    margin: 0 auto;
    .age-calc {
        background-color: $white;
        padding: 34px;
        border-radius: 10px 10px 120px 10px;
        // -webkit-border-radius: 10px 10px 120px 10px;
        // -moz-border-radius: 10px 10px 120px 10px;
        // -ms-border-radius: 10px 10px 120px 10px;
        // -o-border-radius: 10px 10px 120px 10px;
        width: 550px;
        margin: 0 auto;
        .inputs {
            display: flex;
            align-items: center;
            gap: 30px;
            margin-bottom: 20px;
            .input {
                display: flex;
                flex-direction: column;
                gap: 4px;
                position: relative;
                label {
                    font-weight: 600;
                    font-size: 12px;
                    text-transform: uppercase;
                    transform: scale(0.85) translateX(-9px);
                    // -webkit-transform: scale(0.85) translateX(-9px);
                    // -moz-transform: scale(0.85) translateX(-9px);
                    // -ms-transform: scale(0.85) translateX(-9px);
                    // -o-transform: scale(0.85) translateX(-9px);
                    transition: color 0.3s;
                    // -webkit-transition: color 0.3s;
                    // -moz-transition: color 0.3s;
                    // -ms-transition: color 0.3s;
                    // -o-transition: color 0.3s;
                }
                
                @media (max-width: 540px) {
                    label {
                        transform: scale(0.85) translateX(-5px);
                        // -webkit-transform: scale(0.85) translateX(-5px);
                        // -moz-transform: scale(0.85) translateX(-5px);
                        // -ms-transform: scale(0.85) translateX(-5px);
                        // -o-transform: scale(0.85) translateX(-5px);
                    }
                }
                input {
                    width: 110px;
                    padding: 8px 10px;
                    border: solid 1px $light-grey;
                    font-weight: 600;
                    font-size: 22px;
                    cursor: pointer;
                    border-radius: 5px;
                    // -webkit-border-radius: 5px;
                    // -moz-border-radius: 5px;
                    // -ms-border-radius: 5px;
                    // -o-border-radius: 5px;
                    transition: border-color 0.3s;
                    // -webkit-transition: border-color 0.3s;
                    // -moz-transition: border-color 0.3s;
                    // -ms-transition: border-color 0.3s;
                    // -o-transition: border-color 0.3s;
                    &:hover,
                    &:focus {
                        border-color: $purple;
                    }
                    &::placeholder {
                        color: $smokey-grey;
                    }
                }
                
                @media (max-width: 540px) {
                    input {
                        width: 70px;
                        font-size: 18px;
                    }
                }
            }
            .invalid-message {
                color: $light-red;
                position: absolute;
                bottom: -24px;
                font-size: 12px;
                transform: scale(0.76) translateX(-22px);
                // -webkit-transform: scale(0.76) translateX(-22px);
                // -moz-transform: scale(0.76) translateX(-22px);
                // -ms-transform: scale(0.76) translateX(-22px);
                // -o-transform: scale(0.76) translateX(-22px);
                width: 150px;
            }
            
            @media (max-width: 540px) {
                .invalid-message {
                    display: none;
                }
            }
        }
        
        @media (max-width: 540px) {
            .inputs {
                gap: 10px;
                justify-content: center;
            }
        }
        .line {
            display: flex;
            align-items: center;
            hr {
                width: 120%;
                border: none;
                border-bottom: solid 1px $light-grey;
                position: relative;
            }
            button {
                background-color: $purple;
                border: none;
                border-radius: 50%;
                // -webkit-border-radius: 50%;
                // -moz-border-radius: 50%;
                // -ms-border-radius: 50%;
                // -o-border-radius: 50%;
                padding: 10px;
                user-select: none;
                cursor: pointer;
                transition: background-color 0.3s;
                // -webkit-transition: background-color 0.3s;
                // -moz-transition: background-color 0.3s;
                // -ms-transition: background-color 0.3s;
                // -o-transition: background-color 0.3s;
                &:hover {
                    background-color: $off-black;
                }
                img {
                    cursor: inherit;
                    transform: scale(0.85);
                    // -webkit-transform: scale(0.85);
                    // -moz-transform: scale(0.85);
                    // -ms-transform: scale(0.85);
                    // -o-transform: scale(0.85);
                }
            }
            
            @media (max-width: 992px) {
                button {
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                //   -webkit-transform: translateX(-50%);
                //   -moz-transform: translateX(-50%);
                //   -ms-transform: translateX(-50%);
                //   -o-transform: translateX(-50%);
                    width: 50px;
                    height: 50px;
                }
            }
        }
        
        @media (max-width: 992px) {
            .line {
                height: 50px;
                margin-top: 30px;
            }
        }
        
        @media (max-width: 540px) {
            .line {
                margin-top: 0;
            }
        }
        .results{
            div {
                font-size: 60px;
                font-weight: bold;
                letter-spacing: -3px;
                line-height: 1.2;
                span{
                    &:first-child {
                        display: inline-block;
                        color: $purple;
                        letter-spacing: 8px;
                        width: 120px;
                    }
                    @media (max-width: 540px) {
                        span {
                            &:first-child{
                                width: 90px;
                            }
                        }
                    }
                    &.fix-y {
                        letter-spacing: -8px;
                    }
                }
            }
            
            @media (max-width: 540px) {
                div {
                    font-size: 40px;
                }
            }
        }
    }

    @media (max-width: 992px) {
        .age-calc {
            width: fit-content;
        }
    }
    
    @media (max-width: 540px) {
        .age-calc {
            width: 100%;
        }
    }
}