@import "./variables";
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    cursor: auto;
    color: $off-black;
    &::before{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        cursor: auto;
        color: $off-black;
    }
    &::after{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        cursor: auto;
        color: $off-black;
    }
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: $off-white;
    min-height: 100vh;
    padding-top: 80px;
}

.container {
    width: 1100px;
    margin: 0 auto;
}

@media (max-width: 1200px) {
    .container {
        width: 900px;
    }
}

@media (max-width: 992px) {
    .container {
        width: 650px;
    }
}

@media (max-width: 768px) {
    .container {
        width: 520px;
    }
}

@media (max-width: 540px) {
    .container {
        width: 90%;
    }
}

input,
button {
    outline: none;
}

img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    height: 100%;
}