@import "./variables";

footer {
    background-color: $white;
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
    padding: 20px;
    display: flex;
    justify-content: space-around;
    border-top: 1px solid #a7a7a7;
    p{
        font-size: 18px;
        a {
            color: $purple;
            user-select: none;
            cursor: pointer;
            transition: opacity 0.3s;
            // -webkit-transition: opacity 0.3s;
            // -moz-transition: opacity 0.3s;
            // -ms-transition: opacity 0.3s;
            // -o-transition: opacity 0.3s;
            &:hover {
                opacity: .8;
            }
        }
    }
    .date {
        display: flex;
        align-items: center;
        gap: 30px;
        time {
            width: 90px;
            font-size: 18px;
            letter-spacing: 1px;
        }
    }
}
@media (max-width: 992px) {
    footer {
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }
}